<template>
  <div class="side-wrapper">
    <div class="side-menu">
      <router-link to="/admin/posts">
        <div class="side-item border-bottom"></div>
      </router-link>
      <router-link to="/admin/companies">
        <div class="side-item border-bottom"></div>
      </router-link>
      <router-link to="/admin/entrepreneurs">
        <div class="side-item border-bottom"></div>
      </router-link>
    </div>
    <ul class=" post-item post-menu-content">
      <router-link to="/admin/posts-new"><li class="create-item border-bottom"></li></router-link>
      <router-link to="/admin/companies-new"><li class="create-item border-bottom"></li></router-link>
      <router-link to="/admin/entrepreneurs-new"><li class="create-item"></li></router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Sidebar'
}
</script>

<style lang="stylus" scoped>
  @import '~styles/variables.styl'
  .side-wrapper >>> .router-link-active
    .side-item
      background: $bgColor
    .create-item
      color: $fontColor
  .side-wrapper
    float: left
    width: 20%
    .side-menu
      box-sizing: border-box
      border: 1px solid red
      font-size: 16px
      border: 1px solid $borderColor
      margin-bottom: 30px
      .side-item
        text-align: center
        padding: 10px
        cursor: pointer
        color: #666
        &:hover
          background: $bgColor
          color: #fff
    .post-item
      background: $bgColor
      color: #fff
      text-align: center
    .post-menu-content li
      padding: 10px;
      color: #fff
</style>



